import { Component } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth';
import { ToastController } from 'ionic-angular';

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  email: string;
  password: string;
  emailCtrl: FormControl;
  passwordCtrl: FormControl;
  loginForm: FormGroup;
  isSubmitting: boolean = false;
  userId: string = '';
  isAdmin: number = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, public authProvider: AuthProvider, private toastCtrl: ToastController) {
    this.emailCtrl = new FormControl('', [Validators.required, Validators.pattern(EMAIL_REGEX)]);
    this.passwordCtrl = new FormControl('', Validators.required);

    this.loginForm = new FormGroup({
      email: this.emailCtrl,
      password: this.passwordCtrl
    });
  }
  
  ionViewDidEnter() {
    if(this.userId != '') {
      if(this.isAdmin == 1) { // admin redirect
        this.navCtrl.push('AdminHomePage');
      }else { // normal user redirect
        this.navCtrl.push('TabsPage');
      }
    }else {
      return true;
    }
    // this.authProvider.isAuthenticated().then(isAuth => {
    //   if(isAuth) {
    //     this.authProvider.loadProfile(this.userId).then(res => {
    //       if(res['isAdmin'] == 1) { // admin redirect
    //         this.navCtrl.push('AdminHomePage');
    //       }else { // normal user redirect
    //         this.navCtrl.push('TabsPage');
    //       }
    //     }).catch(err => {
    //       console.log(err);
    //     });
    //   }else {
    //     return true;
    //   }
    // });
  }

  goToRegisterPage() {
    this.navCtrl.push('RegisterPage');
  }

  loginSubmit() {
    if(this.loginForm.valid) {
      this.isSubmitting = true;
      this.authProvider.login(this.email, this.password).then(user => {
        console.log('user', user)
          if (user['emailVerified']) {
            this.isSubmitting = false;
            this.userId = user['uid'];
            localStorage.setItem('userId', user['uid']);
            localStorage.setItem('email', user['email']);
            this.authProvider.loadProfile(user['uid']).then(res => {
              localStorage.setItem('isAdmin', res['isAdmin']);
              if(res['isAdmin'] == 1) { // admin redirect
                this.isAdmin = 1;
                this.navCtrl.push('AdminHomePage');
              }else { // normal user redirect
                this.isAdmin = 0;
                this.navCtrl.push('TabsPage');
              }
            }).catch(err => {
              this.isSubmitting = false;
              let toast = this.toastCtrl.create({
                message: 'Invalid email or password',
                duration: 5000,
                position: 'bottom'
              });
        
              toast.present();
            });
          }else {
            this.isSubmitting = false;
            let toast = this.toastCtrl.create({
              message: 'Login failed, your email address is not verified yet',
              duration: 5000,
              position: 'bottom'
            });
      
            toast.present();
          }
      }).catch(err => {
        this.isSubmitting = false;
        let toast = this.toastCtrl.create({
          message: 'Invalid email or password',
          duration: 5000,
          position: 'bottom'
        });
  
        toast.present();
      });
    }else { 
      this.isSubmitting = false;
      let toast = this.toastCtrl.create({
        message: 'Login failed',
        duration: 5000,
        position: 'bottom'
      });

      toast.present();
    }
  }
}



